.item {
    width:20%;
    animation: appear 0.8s;
}
.item__img {
    display: block;
    margin: 15px auto 15px;
}
.item__text {
    text-align: center;
}
.item__header {
    text-align: center;
}
@keyframes appear {
    from { 
      transform: translateY(150%);
      opacity: 0.2;
    }
  }